
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import { validateNotNull } from 'util/rules'
import Scroll from 'assembly/scroll/scroll'
import Timer from 'assembly/timer/timer'

export default {
data () {
return {
clientId: '',
mobile: '',
passwd: '',
repasswd: '',
code: ''
}
},
methods: {
sendCode () {
if (!this.$refs.timer.disabled) { // 如果没在锁定状态
if (validateNotNull(this, this.clientId, '客户ID')) {
this.$tupHttp(this, 'front/login/mobile?clientId=' + this.clientId).then(res => {
this.mobile = res.data.model
this.$refs.timer.start()
this.$tupHttp(this, 'front/login/send?mobile=' + this.mobile).then(res => {
this.$vux.toast.text('验证码已发送')
})
})
}
}
},
sendEmail () {
if (validateNotNull(this, this.clientId, '客户ID') && this.validateNotMobile() && validateNotNull(this, this.code, '验证码') && validateNotNull(this, this.passwd, '密码') && this.validateRepeatPsw()) {
this.$tupHttp(this, 'front/login/forgetPassword?clientId=' + this.clientId + '&mobile=' + this.mobile + '&code=' + this.code + '&passwd=' + this.passwd).then(res => {
this.$vux.toast.text('密码修改成功')
setTimeout(() => {
this.$router.go(-1)
}, 1500)
})
}
},
validateNotMobile () {
if (!this.mobile) {
this.$vux.toast.text('请发送验证码至手机')
return false
}
return true
},
validateRepeatPsw () {
if (!this.repasswd) {
this.$vux.toast.text('请输入确认密码')
return false
}
if (this.passwd !== this.repasswd) {
this.$vux.toast.text('确认密码不正确')
return false
}
return true
}
},
components: {
XInput,
Group,
XButton,
XHeader,
Scroll,
Timer
}
}



